.normal {
    color: rgb(91, 20, 20);
    background-color: lightgreen;
    font-size: 20px;
    font-weight: bold;
    border-style: solid;
    border-radius: 10px;
    padding: 10px;
    margin-bottom: 10px;
}

.error {   
    color: black;
    background-color: rgb(252, 93, 93);
    font-size: 20px;
    font-weight: bold;
    border-style: solid;
    border-radius: 10px;
    padding: 10px;
    margin-bottom: 10px;
}

.header1 {
    color:black;
    font-size: 50px;
    text-shadow: 2px 2px lightgray;
}

.header2 {
    color: black;
    font-size: 40px;
    text-shadow: 2px 2px lightgray;
}

input {
    box-shadow: 1px 1px darkslategrey;
}

button {
    color: black;
    box-shadow: 2px 2px black;
    transition-duration: 0.4s;
}

button:active {
    position: relative;
    top: 1px;
    box-shadow: 1px 1px black;
}

button:hover {
    background-color: black;
    color: white;
}